<template>
  <div class="CommentFormComponent component">
    <form @submit.prevent="handleForm">
      <div class="errors">
        <div class="errors__error" v-for="(error, key, i) in newCommentErrors" :key="key">
          <div class="errors__error__text" v-if="key !== 'text' && key !== 'nickName'">
            {{ error.message }}
          </div>
        </div>
      </div>
      <div class="textarea">
        <textarea rows="5" class="textarea__field" :class="{ 'input__field--red': newCommentErrors.text?.path }"
          v-model="text" :placeholder="textAreaPlaceholder">
      </textarea>
        <div class="errors">
          <div class="errors__error" v-for="(error, key, i) in newCommentErrors" :key="key">
            <div class="errors__error__text" v-if="key === 'text'">
              {{ error.message }}
            </div>
          </div>
        </div>
      </div>
      <div class="input">
        <input class="input__field" type="text" :class="{ 'input__field--red': newCommentErrors.nickName?.path }"
          v-model="nickName" :placeholder="nickPlaceholder">
        <button class="input__button" type="submit">
          <svg xmlns="http://www.w3.org/2000/svg" fill="#fff" height="1em" viewBox="0 0 640 512">
            <!--! Font Awesome Free 6.4.0 by @fontawesome - https://fontawesome.com License - https://fontawesome.com/license (Commercial License) Copyright 2023 Fonticons, Inc. -->
            <path
              d="M88.2 309.1c9.8-18.3 6.8-40.8-7.5-55.8C59.4 230.9 48 204 48 176c0-63.5 63.8-128 160-128s160 64.5 160 128s-63.8 128-160 128c-13.1 0-25.8-1.3-37.8-3.6c-10.4-2-21.2-.6-30.7 4.2c-4.1 2.1-8.3 4.1-12.6 6c-16 7.2-32.9 13.5-49.9 18c2.8-4.6 5.4-9.1 7.9-13.6c1.1-1.9 2.2-3.9 3.2-5.9zM0 176c0 41.8 17.2 80.1 45.9 110.3c-.9 1.7-1.9 3.5-2.8 5.1c-10.3 18.4-22.3 36.5-36.6 52.1c-6.6 7-8.3 17.2-4.6 25.9C5.8 378.3 14.4 384 24 384c43 0 86.5-13.3 122.7-29.7c4.8-2.2 9.6-4.5 14.2-6.8c15.1 3 30.9 4.5 47.1 4.5c114.9 0 208-78.8 208-176S322.9 0 208 0S0 78.8 0 176zM432 480c16.2 0 31.9-1.6 47.1-4.5c4.6 2.3 9.4 4.6 14.2 6.8C529.5 498.7 573 512 616 512c9.6 0 18.2-5.7 22-14.5c3.8-8.8 2-19-4.6-25.9c-14.2-15.6-26.2-33.7-36.6-52.1c-.9-1.7-1.9-3.4-2.8-5.1C622.8 384.1 640 345.8 640 304c0-94.4-87.9-171.5-198.2-175.8c4.1 15.2 6.2 31.2 6.2 47.8l0 .6c87.2 6.7 144 67.5 144 127.4c0 28-11.4 54.9-32.7 77.2c-14.3 15-17.3 37.6-7.5 55.8c1.1 2 2.2 4 3.2 5.9c2.5 4.5 5.2 9 7.9 13.6c-17-4.5-33.9-10.7-49.9-18c-4.3-1.9-8.5-3.9-12.6-6c-9.5-4.8-20.3-6.2-30.7-4.2c-12.1 2.4-24.7 3.6-37.8 3.6c-61.7 0-110-26.5-136.8-62.3c-16 5.4-32.8 9.4-50 11.8C279 439.8 350 480 432 480z" />
          </svg>
        </button>
      </div>
    </form>
    <div class="errors errors--input">
      <div class="errors__error" v-for="(error, key, i) in newCommentErrors" :key="key">
        <div class="errors__error__text" v-if="key === 'nickName'">
          {{ error.message }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import HeaderComponent from "@/components/HeaderComponent.vue";

export default {
  name: "CommentFormComponent",
  components: { HeaderComponent },
  props: {
    postNewComment: {
      type: Function,
      required: true
    },
    parent: {
      type: String,
      default: null
    },
    textAreaPlaceholder: {
      type: String,
      default: 'Wpisz treść komentarza'
    },
    nickPlaceholder: {
      type: String,
      default: 'Podpisz się'
    }
  },
  data() {
    return {
      text: '',
      nickName: '',
      newCommentErrors: {}
    }
  },
  computed: {

  },
  methods: {
    handleForm() {
      this.postNewComment({ text: this.text, nickName: this.nickName, parent: this.parent }).then(data => {
        this.resetForm()
        this.$emit('commentFormSent')
      }, error => {
        this.newCommentErrors = { ...error.errors }
      })
    },
    resetForm() {
      this.text = '';
      this.nickName = ''
      this.newCommentErrors = {}
    }
  }
}
</script>

<style lang="scss" scoped>
$inputColor: #3290FA;
$errorColor: red;

.textarea {
  margin-bottom: 1.5em;

  &__field {
    width: 100%;
    padding: 1em;
    border: 2px solid $inputColor;
    border-radius: 2em;
    font-size: 2em;
    transition: all 0.25s;
  }
}

.input {

  width: 80%;

  @media screen and (min-width: 1000px) {
    width: 60%;
  }

  position: relative;
  margin-left: auto;

  &__field {
    padding: 1em 9em 1em 1em;
    width: 100%;
    border-radius: 2em;
    font-size: 2em;
    border: 2px solid $inputColor;
    transition: all 0.25s;

    &--red {
      border-color: red;
    }
  }

  &__button {
    display: block;
    position: absolute;
    right: 0;
    top: 0;
    height: 100%;

    width: 8em;

    @media screen and (min-width: 1000px) {
      width: 13em;
    }

    border: none;
    border-radius: 2em;
    background-color: $inputColor;
    cursor: pointer;
    transition: all 0.25s;

    &:hover {
      background-color: black;
    }

    svg {
      font-size: 2em;

      @media screen and (min-width: 1000px) {
        font-size: 3em;
      }
    }
  }
}

.errors {
  text-align: center;
  // padding-right: 13em;
  color: $errorColor;
  margin-bottom: 3em;

  &__error {
    margin-top: 0.5em;

    &__text {
      font-size: 3em;
    }
  }

  &--input {
    width: 80%;
    margin: 0 0 0 auto;

    @media screen and (min-width: 1000px) {
      width: 60%;
    }
  }

}
</style>